.cmn-chkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.cmn-chkbox-container {
  display: block;
  position: relative;
  font-size: 20px !important;
  user-select: none;
  margin-right: 10px !important;
}

.cmn-checkmark {
  position: relative;
  top: 0;
  left: 0;
  cursor: pointer;
  height: 1.2em;
  width: 1.2em;
  transition: all 0.3s;
  border-radius: 4px;
  border: 1px solid #f27b1a;
}

.cmn-chkbox-container input:checked ~ .cmn-checkmark {
  background-color: #f27b1a;
  animation: checkpop 0.5s;
  animation-direction: alternate;
}

.cmn-chkbox-container .cmn-checkmark:focus {
  box-shadow: 0 0 0 0.2rem rgba(130, 138, 145, 0.5);
}

.cmn-checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.cmn-chkbox-container input:checked ~ .cmn-checkmark:after {
  display: block;
}

.cmn-chkbox-container .cmn-checkmark:after {
  left: 0.42em;
  top: 0.2em;
  width: 0.3em;
  height: 0.57em;
  border: solid white;
  border-width: 0 0.15em 0.15em 0;
