/* The switch - the box around the slider */
.switch-new {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 22px;
}

/* Hide default HTML checkbox */
.switch-new input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider-new {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #e5e5e5;
  box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider-new:before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  left: 1px;
  bottom: 1px;
  background: #ffffff;
  box-shadow: 0px 1.48148px 3.7037px rgba(113, 113, 113, 0.392674);
  border-radius: 3.7037px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider-new {
  background-color: #28a025;
}

/* input:checked + .slider-new .on-new{
  display: block;
}
input:checked + .slider-new .off-new{
  display: none;
} */
/* input:focus+.slider-new {
  box-shadow: 0 0 1px #28A025;
} */

input:checked + .slider-new:before {
  -webkit-transform: translateX(22px);
  -ms-transform: translateX(22px);
  transform: translateX(22px);
}

/* Rounded sliders */
.slider-new.round {
  border-radius: 34px;
}

.slider-new.round:before {
  border-radius: 50%;
}

.on-new,
.off-new {
  font-size: 12px;
  line-height: 14px;
